import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'
import Index from '../components/Index'
import Skill from '../components/Skill'
import Developer from '../components/Developer'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
      path: '/login',
      component: Login
    },
    {
      path: '/index',
      component: Index
    },
    {
      path: '/develpoer',
      component: Developer
    },
    {
      path: '/skill',
      component: Skill
    },
  ]
})



